.jumbotron {
    width: 100%;
    min-height: 300px;
    padding-top: 100px;
    margin: 0px;
    margin-bottom: 5%;
    display: table;
    padding: 180px 20px;
    color: rgb(11, 110, 240);	    
    background-image: url("https://unsplash.com/photos/XN4T2PVUUgk");
    background-size: cover;
	background-attachment: fixed;
}
div.thumbnail {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    padding-top: 100%;
  }
  
